.DisplayItem ul{
    margin: 30px;
    color: #F8BBD0;
    font-size: 23px;
    float: left;
}

.DisplayItem ul li {
    background: #F50057;
    border-radius: 30%;
    padding: 30px;
    overflow: hidden;
    transition: color 1s, background 1s;
}

.DisplayItem ul li:hover {
    color: #C51162;
    background: #FF80AB;
}

@media (max-width: 415px) {
    .DisplayItem ul{
        margin: 5px;
        font-size: 14px;
    }
    .DisplayItem ul li {
        border-radius: 30%;
        padding: 15px;
    }
}
