.AddToSumCSS {
    padding: 0;
    margin: 0;
}

.AddToSumCSS h3 {
    font-size: 36px;
    color: white;
    background: #263238;
    padding: 30px;
}

.AddToSumCSS input {
    border: none;
    border-radius: 10px;
    height: 40px;
    width: 40%;
    padding: 10px;
    font-family: 'Patua One', cursive;
    font-size: 30px;
    color: #455A64;
}

.AddToSumCSS ul{
    text-decoration: none;
    list-style-type: none;
}

.Display {
    height: 60px;
    color: #81D4FA;
    font-size: 36px;
    margin-bottom: 30px;
    
}

.InputBTN button{
    cursor: pointer;
    height: 50px;
    width: 120px;
    margin: 20px 1px;
    border: none;
    border-radius: 10px;
    font-family: 'Patua One', cursive;
    font-size: 20px;
    transition: color 0.7s, background 0.7s;
}

.InputBTN button:hover {
    color: #1A237E;
    background: #40C4FF;
}

.ActionBTN button {
    cursor: pointer;
    height: 50px;
    width: 150px;
    margin: 10px 5px 30px;
    padding: 5px;
    border: none;
    border-radius: 10px;
    font-family: 'Patua One', cursive;
    font-size: 24px;
    transition: color 0.7s, background 0.8s, height 0.2s, width 0.2s;
}

.ActionBTN button:hover {
    color: #E0E0E0;
    background: #212121;
}

.ActionBTN button:active {
    height: 65px;
    width: 165px;

}

@media (max-width: 415px) {
    
    .AddToSumCSS h3 {
        padding: 20px;
        font-size: 30px;
    }
    .AddToSumCSS input {
        width: 300px;
    }
    .Display {
        height: 40px;
        font-size: 28px;
        margin-bottom: 18px;
    }
    .InputBTN button{
        height: 30px;
        width: 80px;
        margin: 5px;
        font-size: 16px;
    }
    .ActionBTN button {
        height: 30px;
        width: 100px;
        margin: 30px 5px 5px;
        font-size: 16px;
        float: left;
        margin-bottom: 50px;
    }

    .ActionBTN button:active {
        height: 30px;
        width: 100px;
    }
}

@media (max-width: 360px) {
    .ActionBTN button {
        height: 40px;
        width: 80px;
        font-size: 14px;

    }
}